rn cadastralData == {} ? null : cadastralData\r\n                }\r\n            }\r\n            return null;\r\n        },\r\n    },\r\n    methods: {\r\n        ...mapActions(['editItem']),\r\n        async changeItem(){\r\n            await this.editItem('show')\r\n        },\r\n        async updateCadastralNumberInfo(number) {\r\n            // if(this.cadastralData) {\r\n            this.loading = true;\r\n                await axios.post('/api/admin/lots/update-cadastral-number-info', {id: this.item.id, cadastralNumber: number})\r\n                    .then((response) => {\r\n                        console.log('response', response.data.result);\r\n                        if(response.data.result) {\r\n                            this.$store.commit('setModal', {data: 'success', text: '  '});\r\n                            this.changeItem()\r\n                        }\r\n                        else {\r\n                            this.$store.commit('setModal', {data: 'error', text: '   '});\r\n                        }\r\n                        this.loading = false;\r\n                    }).catch(error => {\r\n                        console.log(error);\r\n                        this.loading = false;\r\n                        this.$store.commit('setModal', {data: 'error', text: ' '});\r\n                        throw error\r\n                    });\r\n            // }\r\n        },\r\n        async updateCadastralInfo() {\r\n            this.loading = true;\r\n            await axios.post('/api/admin/lots/update-cadastral-info', {\r\n                id: this.item.id,\r\n            })\r\n                .then((response) => {\r\n                    console.log('response', response.data.result);\r\n                    if (response.data.result) {\r\n                        this.$store.commit('setModal', {data: 'success', text: '  '});\r\n                        this.changeItem()\r\n                    }\r\n                    this.loading = false;\r\n                }).catch(error => {\r\n                    console.log(error);\r\n                    this.loading = false;\r\n                    this.$store.commit('setModal', {data: 'error', text: ' '});\r\n                    throw error\r\n                });\r\n\r\n        },\r\n        async deleteParam(id) {\r\n            this.loading = true;\r\n            await axios.delete('/api/admin/lots/param/'+id)\r\n                .then((response) => {\r\n                    console.log('response', response.data);\r\n                    if (response.data.result) {\r\n                        this.$store.commit('setModal', {data: 'success', text: '  '});\r\n                        this.changeItem()\r\n                    }\r\n                    this.loading = false;\r\n                }).catch(error => {\r\n                    console.log(error);\r\n                    this.loading = false;\r\n                    this.$store.commit('setModal', {data: 'error', text: ' '});\r\n                    throw error\r\n                });\r\n\r\n        }\r\n    },\r\n    async created() {\r\n        this.$store.commit('setCurrentRoute', this.$route.path.replace(/(\\/*$)/, \"\"))\r\n        await this.editItem('show')\r\n    },\r\n}\r\n</script>\r\n\r\n<style scoped>\r\na {\r\n    color: var(--bs-link-color);\r\n    text-decoration: underline;\r\n}\r\n\r\nul {\r\n    margin-bottom: 0;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
