<template>
    <fragment>
        <div class="content-header" v-if="!simple">
            <div class="container-fluid">
                <div class="row mb-2">
                    <div class="col-sm-6">
                        <h1 class="m-0">
                            {{ isEdit ? '   .' : '  ' }}</h1>
                    </div>
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <li class="breadcrumb-item">
                                <router-link to="/admin/additions"
                                             style="color: #007bff; text-decoration: none; background-color: transparent;">
                                       .   
                                </router-link>
                            </li>
                            <li class="breadcrumb-item active">{{ isEdit ? '' : '' }}</li>
                        </ol>
                    </div>
                </div>
            </div>
        </div>

        <section class="content">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-12">
                        <div class="card card-primary">
                            <div class="card-body">
                                <template v-if="!simple">
                                    <div class="form-group" v-if="isEdit">
                                        <label>  </label>
                                        <a :href="'/admin/lots/'+ item.lotId" target="_blank"
                                           style="color: #007bff; text-decoration: none; background-color: transparent;">
                                              
                                        </a>
                                    </div>
                                    <div class="form-group" v-else>
                                        <label>  (        )</label>
                                        <admin-select :options="lots" @search="getLots" v-model="item.lotId" @selected="setLot"></admin-select>
                                    </div>
                                </template>

                                <div class="form-group" v-if="item.initial_message">
                                    <label> </label>
                                    <textarea v-model="item.initial_message" class="form-control" rows="10" name="initial_message" readonly></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="bkt-wrapper-between">
                                        <label> </label>
                                        <button v-if="item.message" type="button"
                                                class="btn btn-outline-primary bkt-button-icon primary-outline bkt-hover-primary"
                                                @click="processText(item.message)"
                                                title="  "
                                        >
                                            <bkt-icon name="Refresh" color="primary" :width="'16px'" :height="'16px'"/>
                                        </button>
                                    </div>
                                   <!--- <vue-editor v-model="item.message"
                                                :editor-toolbar="customToolbar"
                                                :editorOptions="editorOptions">
                                    </vue-editor> ----->
                                    <textarea v-model="item.message" class="form-control" required rows="10" name="message"></textarea>
                                </div>
                                <div class="form-group" v-if="isEdit">
                                    <files-table
                                                 :files="item.files"
                                                 :is-images="false"
                                                 type="additions"
                                                 :id="item.id"
                                                 @change="editItem">
                                    </files-table>
                                </div>
                                <div class="form-group" v-if="isEdit">
                                    <files-table
                                                 :files="item.images"
                                                 :is-images="true"
                                                 type="additions"
                                                 :id="item.id"
                                                 @change="editItem">
                                    </files-table>
                                </div>
                                <div class="form-group">
                                    <label>   ?</label>
                                    <admin-switch
                                                  index="1"
                                                  :model="item.isModerated"
                                                  @change="item.isModerated = !item.isModerated"
                                    >
                                    </admin-switch>
                                </div>
                                <div class="bkt-wrapper bkt-gap-small justify-content-end">
                                    <button type="submit" class="btn btn-success float-right"
                                            @click="isEdit ? editLot(item) : addData(item)">
                                        
                                    </button>
                                    <button type="submit" class="btn btn-default float-right" v-if="simple"
                                            @click="$emit('cancel')">
                                        
                                    </button>
                                </div>

                            </div>
                            <!-- /.card-body -->
                        </div>
                        <!-- /.card -->
                    </div>
                    <div class="col-md-12" v-if="isEdit">
                        <div class="card card-primary">
                            <div class="card-body">
                                <div class="bkt-wrapper bkt-wrapper-between my-2">
                                    <div> : {{item_rating}}</div>
                                    <button type="button"
                                            class="btn btn-outline-primary bkt-button-icon primary-outline bkt-hover-primary"
                                            data-bs-toggle="modal" data-bs-target="#answerRatingModal" :disabled="loading">
                                        <bkt-icon name="Plus" color="primary" :width="'16px'" :height="'16px'"/>
                                    </button>
                                </div>
                                <div class="bkt-wrapper-column bkt-gap-small"
                                     style="max-height: 600px; overflow-y: auto">
                                    <div v-for="(rating,index) in ratings" class="bkt-border-neutral bkt-border-rounded p-2">
                                        <div class="bkt-wrapper bkt-wrapper-between">
                                            <span>: {{rating.estimate}}</span>
                                            <button
                                                type="button"
                                                class="btn btn-outline-red bkt-button-icon red-outline bkt-hover-red"
                                                @click="deleteRating(index,rating.id)"
                                                title=" "
                                                :disabled="loading"
                                            >
                                                <bkt-icon name="Trash" color="red" :width="'16px'" :height="'16px'"/>
                                            </button>
                                        </div>
                                        <p v-if="rating.comment">{{rating.comment}}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <rating-modal id="answerRatingModal" ref="answerRatingModal" type="answer" :answer-id="item.id" @save="createRating" admin/>
                    </div>
                </div>
            </div>
        </section>
    </fragment>
</template>

<script>
import AdminSwitch from "../../AdminSwitch";
import {mapActions, mapGetters} from "vuex";
import FilesTable from "../../FilesTable";
import {VueEditor} from "vue2-editor";
import AdminSelect from "../../AdminSelect";
import axios from "axios";
import Textarea from "../../../components/Textarea";
import RatingModal from "../../../pages/LotCard/RatingModal";

export default {
    name: "AddEditAdditions",
    components: {Textarea, AdminSelect, VueEditor, FilesTable, AdminSwitch, RatingModal},
    props: {
        simple: {
            type: Boolean,
            default: false
        },
        answer_id: {
            type: Number,
            default: null
        }
    },
    data() {
        return {
           /* customToolbar: [
                [{'header': [1, 2, 3, 4, 5, 6, false]}, 'bold', 'italic', 'underline', 'strike'],
                ['blockquote', 'link', 'code-block'],
                [{'header': 1}, {'header': 2},],
                [{'list': 'ordered'}, {'list': 'bullet'}],
                [{'align': ''}, {'align': 'center'}, {'align': 'right'}, {'align': 'justify'}],
                [{'color': []}, {'background': []}],
                ['clean']
            ],
            editorOptions: {
                formats: [
                    'background',
                    'bold',
                    'color',
                    'code',
                    'italic',
                    'link',
                    'size',
                    'strike',
                    'script',
                    'underline',
                    'blockquote',
                    'header',
                    'indent',
                    'list',
                    'align',
                    'code-block'
                ]
            },*/
            lot: null,
            lots: [],
            item_rating: 0,
            ratings:[],
            loading: false
        }
    },
    async created() {
        this.$store.commit('setCurrentRoute', this.simple ? '/admin/additions/'+this.answer_id : this.$route.path.replace(/(\/*$)/, ""))
        this.$store.commit('setItem',
            {message: '', files: [], images: [], isModerated: false, lotId: null}
        )
        if (this.isEdit) {
            await this.editItem();
            this.getRatings();
            this.item_rating = this.item.rating;
        }else{
            await this.getLots()
        }
    },
    computed: {
        ...mapGetters(['item']),
        isEdit() {
            return !this.$route.path.includes('store')
        }
    },
    methods: {
        ...mapActions(['changeStatusAdmin', 'editItem', 'updateData', 'addData']),
        async getLots(page = 1, search = '') {
            await axios({
                method: 'get',
                url: '/api/admin/lots/shorts?search='+search+'&page='+page,
                data: {},
            })
                .then((response) => {
                    this.lots = response.data
                })
                .catch((error) => {
                    console.log(error);
                    this.$store.commit('setModal', {data: 'error', text: '    '})
                });
        },
        setLot(val){
            this.item.lotId = val.code
        },
        async editLot(item) {
            await this.updateData(item)
                .then(resp => {
                    this.$emit('update');
                });
        },
        async processText(message) {
            await axios.post('/api/admin/additions/process-text', {message: message})
                .then((response) => {
                    console.log('response', response.data);
                   this.item.message = response.data;
                }).catch(error => {
                    console.log(error);
                    this.$store.commit('setModal', {data: 'error', text: ' '});
                    throw error
                });
        },
        async getRatings() {
            await axios({
                method: 'get',
                url: '/api/admin/additions/rating',
                data: {answer_id: this.item.id}
            }).then((response) => {
                this.ratings = response.data.ratings
            })
                .catch((error) => {
                    console.log(error);
                    this.$store.commit('setModal', {data: 'error', text: '     '})
                });
        },
        createRating(payload) {
            this.loading = true;
            payload.admin = true;
            axios({
                method: 'post',
                url: '/api/admin/additions/rating/create',
                data: payload
            }).then((response) => {
                this.item_rating = response.data.answer.rating;
                this.ratings.push(response.data.rating);
                this.loading = false;
                if(this.$refs.answerRatingModal) {
                    this.$refs.answerRatingModal.cancel()
                }

            })
                .catch((error) => {
                    console.log(error);
                    this.loading = false;
                    this.$store.commit('setModal', {data: 'error', text: '    '})
                });
        },
        deleteRating(index, id) {
            this.loading = true;
            axios({
                method: 'post',
                url: '/api/admin/additions/rating/delete',
                data: {id: id}
            }).then((response) => {
                this.item_rating = response.data.rating;
                this.ratings.splice(index, 1);
                this.loading = false;
            })
                .catch((error) => {
                    console.log(error);
                    this.loading = false;
                    this.$store.commit('setModal', {data: 'error', text: '    '})
                });
        }
    }
}
</script>

<style scoped>

