<template>
    <AdminTable header="" :columns="columns" :pagination="pagination" :getData="getData">
        <template v-slot:inline-block>
            <div class="row justify-content-end">
                <div class="col-4">
                    <div class="input-group input-group-sm w-100">
                        <input type="text" v-model="param" name="table_search" class="form-control float-right"
                               placeholder="">
                        <div class="input-group-append">
                            <button type="submit" class="btn btn-default" @click="searchParam(param)">
                                <i class="fas fa-search"></i>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template v-slot:raws-block>
            <tr v-for="(item, index) in dataItems">
                <td>{{ item.id }}</td>
                <td>{{ item.name ? item.name : " " }}</td>
                <td>{{ item.email ? item.email : " " }}</td>
                <td>{{ item.phone ? item.phone : " " }}</td>
                <td>{{ item.type }}</td>
                <td>{{ item.paymentId ? item.paymentId : "" }}</td>
                <td>
                    <select v-if="item.tariffId !== 7" v-can="'application-edit'" class="form-control"
                            v-model="item.status"
                            @change="updateData(item)">
                        <option v-for="status in statuses" :value="status.code">{{ status.title }}</option>
                    </select>
                    <p v-if="item.tariffId === 7">{{ statuses.find(status => status.code === item.status).title }}</p>
                </td>
                <td>{{ item.publishDate }}</td>
                <td>
                    <button :id="'button-'+item.id" type="button" class="btn btn-info btn-sm" data-bs-toggle="modal"
                            data-bs-target="#modalView"
                            @click="showModal(index, modalColumns)">
                        <i class="fa fa-eye"></i>
                    </button>
                </td>
            </tr>
            <admin-view-modal :modalData="modalData" :mColumns="mColumns"/>
        </template>
    </AdminTable>
</template>

<script>
import AdminTable from "./AdminTable";
import main from "./mixins/main";
import {mapActions} from "vuex";
import AdminViewModal from "./AdminViewModal";
import axios from "axios";

export default {
    name: "Applications",
    components: {AdminViewModal, AdminTable},
    mixins: [main],
    data() {
        return {
            columns: {
                columns_title: ['#', ' ', '', '', ' ', ' ', '', ' ', ''],
                columns_sort: ['id', 'username', 'email', 'phone', 'type', 'payment_id', 'status',  'created_at', null]
            },
            param: '',

            modalColumns: {
                'id': '', 'name': ' ', 'email': '', 'phone': '',
                'socials': '   ', 'files': ' ', 'topic': '',
                'question': '', 'cadastral_number': ' ', 'dateForAnswer': '  ', 'type': ' ',
                'lotUrl': '  ', 'paymentId': ' ', 'showStatus': ' ', 'publishDate': ' '
            },
            statuses: [
                {'title': ' ', 'code': 'notProcessed'},
                {'title': ' ', 'code': 'inProgress'},
                {'title': ' ', 'code': 'rejectedBySystem'},
                {'title': ' ', 'code': 'rejectedByUser'},
                {'title': '', 'code': 'completed'}
            ]
        }
    },
    created() {
        let uri = window.location.search.substring(1)
        let params = new URLSearchParams(uri)
        let id = params.get("id")
        if (id !== null) {
            setTimeout(() => {
                let existsIndex = this.dataItems.map(item => item.id).indexOf(Number(id))
                console.log(existsIndex);
                if (existsIndex !== -1) {
                    this.showModal(existsIndex, this.modalColumns)
                    $("#modalView").modal('show');
                } else {
                    let index = 0
                    console.log(index);
                    axios({
                        method: 'get',
                        url: '/api/admin/applications/' + id,
                        data: {},
                    })
                        .then((response) => {
                            this.dataItems[index] = response.data
                            this.showModal(index, this.modalColumns)
                            $("#modalView").modal('show');
                        })
                        .catch((error) => {
                            console.log(error);
                        });

                }
            }, 400);
        }

    },
    methods: {
        ...mapActions(['updateData']),
        showModal(index, modalColumns) {
            this.dataItems[index]['showStatus'] = this.statuses.find(status => status.code === this.dataItems[index]['status']).title
            this.setModalData(index, modalColumns)
        }
    }
}
</script>

<style scoped>

</style>
